.pagination {
    position: absolute;
    bottom: 20%;
  }

.pagination_button {
  display: inline-block;
  font-family: arial,sans-serif;
  font-size: 18px;
  font-weight: bold;
  color: rgb(68,68,68);
  text-decoration: none;
  user-select: none;
  padding: .2em 1.2em;
  outline: none;
  background: #f5f5f5;
  transition: all .218s ease 0s;
  border: 0;
}

.pagination_button:hover {
  cursor: pointer;
  color: rgb(24,24,24);
}

.pagination_button:active {
  color: rgb(51,51,51);
}

.pagination_active {
  color: brown;
  border: 0;
}
